nav {
    background: rgba(0, 0, 0, 0.3);
    max-width: max-content;
    display: block;
    padding: 0.4rem 0.7rem;
    z-index:2;
    display: flex;
    gap: 0.7rem;
    position: fixed;
    bottom: 2rem;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 3rem;
    backdrop-filter:blur(15px) ;
}

nav a {
 background: transparent;
 padding: 0.8rem;
 border-radius: 50%;
 display: flex;  
 color: var(--color-light);
 font-size: 1.1rem; 
}

nav a:hover {
    background: rgba(0,0,0,0.3);
}
nav a.active {
    background:var(--color-primary);
    color: var(--color-bg);
}